.Navbar {
  width: 100%;
  height: 70px;
  color: rgb(0, 0, 0);
  position: fixed;
  z-index: 200;
  display: flex;
  flex-direction: column;
  /* justify-content: space-evenly; */
  align-items: center;
  top: 0;
  left: 0;
  backdrop-filter: blur(10px);
  line-height: 21px;
  background-color: rgba(238, 238, 238,0.5);
  background-color: rgba(255, 255, 255, 0.8);
  /* background-color: white; */
 
  /* filter: blur(1px); */
  /* background-color: rgba(0, 0, 0,0.1); */
  /* background-color: red; */
  border-bottom: 0.5px  rgba(238, 238, 238,0.8) solid;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;

}

.NavBarBox {
  width: calc(100% - 7%);
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  top: 0;
  left: 0;
  /* background-color: red; */
 
  line-height: 21px;
  /* background-color: rgba(238, 238, 238,0.5); */
  z-index: 100;
  /* background-color: aqua; */
}
.active{
  /* background-color: red; */
  text-decoration: none;
  color: #CF0A0A;
  height: calc(100% - 20px);
  font-weight: bold;
  /* background-color: rgb(0, 0, 0); */
  align-self: bottom;
  border-bottom:4px #CF0A0A solid;
  border-radius: 3px;
 height: calc(100% - 4px) ;
 display: flex;
 justify-content: center;
 align-items: center;
 text-align: center;
 /* background-color: green; */
 width: 100%;

}
.notActive { 
  color: #000000; 
  text-decoration: none; 
  color: #000000;
    text-decoration: none;
    height: 100%;
    display: flex;
    font-weight: bold;
    justify-content: center;
    align-items: center;
} 

.notActive:hover{ 
  color: #CF0A0A; 
  /* color: #DC5F00; */ 
}

.NavTitle {
  font-weight: bold;
  margin: 60px 10px ;
  padding: 20px;
  font-size: 20px;
  /* background-color: red; */
  font-size: 1.5em;
  /* font-size: 1.5vw; */
  color: #ffa500;
  color:#DC5F00;
  color: #000000;
  /* background-color: #CF0A0A; */
}

.Name {
  display: flex;
  flex-direction: row;
  
  
}
.Tick {
  /* margin: 10px; */
  margin-left: 5px;
  width: 17px;
}

.NavItems {
  /* background-color: red; */
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 27%;
    margin: 0px 0px ;
    height: 100%;
align-items:  baseline;
    /* background-color: green; */

}

.Navitem {
   cursor: pointer;
   font-weight: 500;
   width: 50px;
   height: 100%;
   display: flex;
   text-align: center;
   justify-content: center;
   align-items: center;
   /* background-color: #ffa500; */
}

.Resume{
  width: 100px;
  color: white;
  background-color: #CF0A0A;
  text-align: center;
  line-height: 40px;
  margin: 5px 10px ;
  border-radius: 30px;
  cursor: pointer;
  font-weight: bold;
}
.NavItems2{
  height: 100%;
